
import XCircle from 'vux/src/components/x-circle/index.vue'
import Countup from 'vux/src/components/countup/index.vue'

export default {
data() {
return {
currentRate: 0,
footerImg:[
{ img:require('../../assets/img/Shop_reservation.png'), url:'/Reservation'},
{ img:require('../../assets/img/Shop_signing.png'), url:'/Signing'},
{ img:require('../../assets/img/bind_shops.png'), url:'/BindShops'},
{ img:require('../../assets/img/Rent_extraction.png'), url:'/RentExtraction'},
],
percent: 0,
}
},
components: {
XCircle,Countup
},
beforeCreate(){

},
computed: {
index(){
return this.$store.state.index
},
totalIncome(){
return parseFloat(this.index.totalIncome)
},
user(){
return this.$store.state.user
}
},
mounted(){
document.title = '扑满'
this.$store.state.puman_unionId != null ? this.$store.dispatch('index') : ''

let a = setInterval(()=>{
for(let i = 0; i < 75; i++){
this.percent ++
this.percent == 75 ? clearInterval(a) : ''
}
},100)
this.$store.commit('ACTIVE',0)
},
methods: {
name() {

}
},
}



